import { Calendar, CalendarEvent, EventImportance, Month } from '../models/calendar';
import moment, { Moment } from 'moment';
import { EasterFinder } from './easterFinder.service';
import { DefaultColorSettings } from '@/models/colors';

export class CalendarCreatorService {
    public static MoveCalendarToYear(calendar: Calendar, year: number): CalendarEvent[] {
        const isNewYearLeap = moment([year]).isLeapYear();
        const leapDay: Moment | undefined = isNewYearLeap ? moment([year, Month.February, 29]) : undefined;
        const leapEvents: CalendarEvent[] = [];

        const leapCheck: Function = (e: CalendarEvent) => e.date.isSame(leapDay);

        if (!isNewYearLeap && calendar.events.some(e => leapCheck(e))) {
            leapEvents.push(...calendar.events.filter(e => leapCheck(e)));
        }

        if (!isNewYearLeap) {
            calendar.events = calendar.events.filter(e => !leapCheck(e));
        }

        calendar.events.forEach(e => e.date = e.date.add(year - calendar.year, "y"));
        calendar.year = year;

        calendar.events = calendar.events
            .filter(e => !e.isMoveable)
            .concat(this.GenerateNonHolidayEasterEvents(calendar.year))
            .concat(this.GenerateHolidayEasterEvents(calendar.year));

        return leapEvents;
    }

    public static GenerateCalendar(year: number) : Calendar {
        return new Calendar(year, this.GenerateHolidays(year).concat(this.GenerateNonHolidays(year)), new DefaultColorSettings);
    }

    private static GenerateNonHolidays(year: number): CalendarEvent[] {
        const nonMoveableEvents: CalendarEvent[] = [
            { date: moment([year, Month.January, 21]), text: 'Dzie Babci' },
            { date: moment([year, Month.January, 22]), text: 'Dzie Dziadka' },
            { date: moment([year, Month.February, 8]), text: 'wito Suby Wiziennej' },
            { date: moment([year, Month.February, 14]), text: 'Walentynki' },
            { date: moment([year, Month.February, 17]), text: 'Dzie Kota' },
            { date: moment([year, Month.February, 19]), text: 'Dzie Nauki Polskiej' },
            { date: moment([year, Month.February, 22]), text: 'Dzie Ofiar Przestpstw' },
            { date: moment([year, Month.March, 1]), text: 'Narodowy Dzie Pamici "onierzy Wykltych"'},
            { date: moment([year, Month.March, 8]), text: 'Dzie Kobiet' },
            { date: moment([year, Month.March, 21]), text: 'Dzie Wagarowicza' },
            { date: moment([year, Month.March, 23]), text: 'Dzie Przyjani Polsko-Wgierskiej' },
            { date: moment([year, Month.March, 24]), text: 'Narodowy Dzie ycia'},
            { date: moment([year, Month.April, 13]), text: 'Dzie Pamici Ofiar Zbrodni Katyskiej' },
            { date: moment([year, Month.April, 14]), text: 'wito Chrztu Polski' },
            { date: moment([year, Month.April, 18]), text: 'Dzie Pacjenta w piczce' },
            { date: moment([year, Month.April, 19]), text: 'Rocznica Powstania w Gettcie Warszawskim' },
            { date: moment([year, Month.April, 28]), text: 'Dzie Bezpieczestwa i Ochrony Zdrowia w Pracy' },
            { date: moment([year, Month.May, 2]), text: 'wito Flagi' },
            { date: moment([year, Month.May, 4]), text: 'Dzie Straaka' },
            { date: moment([year, Month.May, 16]), text: 'wito Stray Granicznej' },
            { date: moment([year, Month.May, 26]), text: 'Dzie Matki' },
            { date: moment([year, Month.May, 27]), text: 'Dzie Samorzdu Terytorialnego' },
            { date: moment([year, Month.June, 1]), text: 'Dzie Dziecka' },
            { date: moment([year, Month.June, 13]), text: 'Dzie andarmerii Wojskowej' },
            { date: moment([year, Month.June, 23]), text: 'Dzie Ojca' },
            { date: moment([year, Month.July, 1]), text: 'Dzie Psa' },
            { date: moment([year, Month.July, 12]), text: 'Dzie Walki i Mczestwa Wsi Polskiej' },
            { date: moment([year, Month.July, 24]), text: 'wito Policji' },
            { date: moment([year, Month.July, 31]), text: 'Dzie Skarbowoci' },
            { date: moment([year, Month.August, 1]), text: 'Narodowy Dzie Pamici Powstania Warszawskiego' },
            { date: moment([year, Month.August, 15]), text: 'Dzie Wojska Polskiego' },
            { date: moment([year, Month.August, 29]), text: 'Dzie Stray Gminnej' },
            { date: moment([year, Month.August, 31]), text: 'Dzie Solidarnoci i Wolnoci' },
            { date: moment([year, Month.September, 12]), text: 'wito Wojsk Ldowych' },
            { date: moment([year, Month.September, 17]), text: 'wiatowy Dzie Sybiraka' },
            { date: moment([year, Month.September, 22]), text: 'wiatowy Dzie bez Samochodu' },
            { date: moment([year, Month.September, 27]), text: 'Dzie Podziemnego Pastwa Polskiego' },
            { date: moment([year, Month.October, 4]), text: 'wiatowy Dzie Zwierzt' },
            { date: moment([year, Month.October, 14]), text: 'Dzie Edukacji Narodowej' },
            { date: moment([year, Month.October, 19]), text: 'Narodowy Dzie Pamici Duchownych Niezomnych' },
            { date: moment([year, Month.October, 31]), text: 'Halloween' },
            { date: moment([year, Month.November, 8]), text: 'Dzie Zdrowego niadania' },
            { date: moment([year, Month.November, 14]), text: 'wiatowy Dzie Seniora' },
            { date: moment([year, Month.November, 29]), text: 'Andrzejki' },
            { date: moment([year, Month.December, 4]), text: 'Barbrka' },
            { date: moment([year, Month.December, 6]), text: 'Mikoajki' },
            { date: moment([year, Month.December, 13]), text: 'Dzie Pamici Ofiar Stanu Wojennego' },
            { date: moment([year, Month.December, 31]), text: 'Sylwester' },
        ];
        nonMoveableEvents.forEach(e => e.importance == EventImportance.NonHoliday);

        const events: CalendarEvent[] = [];
        events.push(...nonMoveableEvents);
        events.push(...this.GenerateNonHolidayEasterEvents(year));
        events.sort((a, b) => {
            if (a.date < b.date) return -1;
            if (a.date > b.date) return 1;
            else return 0;
        })

        return events;
    }
    private static GenerateHolidays(year: number): CalendarEvent[] {
        const nonMoveableEvents: CalendarEvent[] = [
            { date: moment([year, Month.January, 1]), text: 'Nowy Rok' },
            { date: moment([year, Month.January, 6]), text: 'wito Trzech Krli' },
            { date: moment([year, Month.May, 1]), text: 'wito Pracy' },
            { date: moment([year, Month.May, 3]), text: 'wito Konstytucji Trzeciego Maja' },
            { date: moment([year, Month.July, 15]), text: 'Wniebowzicie Najwitszej Maryi Panny' },
            { date: moment([year, Month.November, 1]), text: 'Wszystkich witych' },
            { date: moment([year, Month.November, 11]), text: 'wito Niepodlegoci' },
            { date: moment([year, Month.December, 25]), text: 'Boe Narodzenie' },
            { date: moment([year, Month.December, 26]), text: 'Boe Narodzenie' },
        ];
        nonMoveableEvents.forEach(e => e.importance == EventImportance.Holiday);

        const events: CalendarEvent[] = [];
        events.push(...nonMoveableEvents);
        events.push(...this.GenerateHolidayEasterEvents(year));
        events.sort((a, b) => {
            if (a.date < b.date) return -1;
            if (a.date > b.date) return 1;
            else return 0;
        })

        return events;
    }

    private static GenerateNonHolidayEasterEvents(year: number): CalendarEvent[] {
        const easter: Moment = EasterFinder.GetDate(year);
        const events: CalendarEvent[] = [
            { date: easter.clone().add(-52, "days"), text: 'Tusty Czwartek' },
            { date: easter.clone().add(-46, "days"), text: 'roda Popielcowa' },
            { date: easter.clone().add(-3, "days"), text: 'Wielki Czwartek' },
            { date: easter.clone().add(-2, "days"), text: 'Wielki Pitek' },
            { date: easter.clone().add(-1, "days"), text: 'Wielka Sobota' },
        ];

        events.forEach(e => {
            e.importance = EventImportance.NonHoliday;
            e.isMoveable = true;
        });

        return events;
    }

    private static GenerateHolidayEasterEvents(year: number): CalendarEvent[] {
        const easter: Moment = EasterFinder.GetDate(year);
        const events: CalendarEvent[] = [
            { date: easter, text: 'Wielkanoc' },
            { date: easter.clone().add(1, "day"), text: 'Poniedziaek Wielkanocny' },
            { date: easter.clone().add(60, "days"), text: 'Boe Ciao' },
            { date: easter.clone().add(7, "weeks"), text: 'Zielone witki' },
        ];

        events.forEach(e => {
            e.importance = EventImportance.Holiday;
            e.isMoveable = true;
        });

        return events;
    }
}